"use client"

import { useState, useEffect, DragEvent } from "react";
import { Plus, MoreHorizontal, X, DollarSign, Calendar, User, Mail, Trash2, Edit2, Paperclip, MessageSquare } from "lucide-react";

// --- Types ---
type OrderStatus = "Pending" | "Processing" | "Completed" | "Cancelled";

interface Order {
    id: string;
    customer: string;
    email: string;
    total: number;
    status: OrderStatus;
    date: string;
    itemsSummary?: string;
    tags?: string[]; // Mock tags for the UI look
}

// --- Mock Data Init ---
const INITIAL_ORDERS: Order[] = [
    { id: "ORD-001", customer: "Amrin", email: "amrin@example.com", total: 150.00, status: "Pending", date: "2024-05-15", itemsSummary: "Business Cards (x500)", tags: ["Urgent"] },
    { id: "ORD-002", customer: "John Doe", email: "john@test.com", total: 85.50, status: "Processing", date: "2024-05-14", itemsSummary: "A5 Flyers", tags: ["New Client"] },
    { id: "ORD-003", customer: "Robert Brown", email: "rob@test.com", total: 210.00, status: "Completed", date: "2024-05-12", itemsSummary: "Banner Setup", tags: ["Paid"] },
    { id: "ORD-004", customer: "Alice Smith", email: "alice@test.com", total: 45.00, status: "Pending", date: "2024-05-16", itemsSummary: "Stickers", tags: [] },
];

export default function AdminOrdersPage() {
    const [orders, setOrders] = useState<Order[]>([]);
    const [isLoaded, setIsLoaded] = useState(false);

    // Modal State
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [editingOrder, setEditingOrder] = useState<Order | null>(null);

    // Load from LocalStorage
    useEffect(() => {
        const saved = localStorage.getItem("web2print_orders");
        if (saved) {
            try {
                setOrders(JSON.parse(saved));
            } catch (e) {
                console.error("Failed to parse orders", e);
                setOrders(INITIAL_ORDERS);
            }
        } else {
            setOrders(INITIAL_ORDERS);
        }
        setIsLoaded(true);
    }, []);

    // Save to LocalStorage
    useEffect(() => {
        if (isLoaded) {
            localStorage.setItem("web2print_orders", JSON.stringify(orders));
        }
    }, [orders, isLoaded]);

    const handleSaveOrder = (order: Order) => {
        if (editingOrder) {
            setOrders(prev => prev.map(o => o.id === order.id ? order : o));
        } else {
            setOrders(prev => [...prev, order]);
        }
        setIsModalOpen(false);
        setEditingOrder(null);
    };

    const handleDeleteOrder = (id: string) => {
        if (confirm("Are you sure you want to delete this order?")) {
            setOrders(prev => prev.filter(o => o.id !== id));
        }
    };

    const updateStatus = (id: string, newStatus: OrderStatus) => {
        setOrders(prev => prev.map(o => o.id === id ? { ...o, status: newStatus } : o));
    };

    // --- Drag and Drop Handlers ---
    const onDragStart = (e: DragEvent, id: string) => {
        e.dataTransfer.setData("orderId", id);
    };

    const onDragOver = (e: DragEvent) => {
        e.preventDefault(); // Necessary to allow dropping
    };

    const onDrop = (e: DragEvent, newStatus: OrderStatus) => {
        const id = e.dataTransfer.getData("orderId");
        if (id) {
            updateStatus(id, newStatus);
        }
    };

    if (!isLoaded) return <div className="p-8">Loading orders...</div>;

    const columns: { title: string, status: OrderStatus, dotColor: string }[] = [
        { title: "Pending", status: "Pending", dotColor: "bg-gray-400" },
        { title: "Processing", status: "Processing", dotColor: "bg-blue-400" },
        { title: "Completed", status: "Completed", dotColor: "bg-green-400" },
        { title: "Cancelled", status: "Cancelled", dotColor: "bg-red-400" },
    ];

    return (
        <div className="h-full flex flex-col p-2">
            <div className="flex justify-between items-center mb-6 px-2">
                <div>
                    <h2 className="text-2xl font-bold text-gray-900">Projects</h2>
                </div>
                <div className="flex gap-2">
                    {/* Filter button mock */}
                    <button className="flex items-center gap-2 px-3 py-1.5 border rounded-lg text-sm font-medium hover:bg-gray-50">
                        Filter <span className="bg-black text-white text-[10px] rounded-full w-4 h-4 flex items-center justify-center">2</span>
                    </button>
                    <button
                        onClick={() => { setEditingOrder(null); setIsModalOpen(true); }}
                        className="flex items-center gap-2 bg-blue-600 text-white px-4 py-1.5 rounded-lg hover:bg-blue-700 transition-colors font-medium text-sm shadow-sm"
                    >
                        <Plus className="h-4 w-4" /> New
                    </button>
                </div>
            </div>

            {/* Kanban Board Container */}
            <div className="flex-1 overflow-x-auto">
                <div className="flex gap-4 min-w-[1000px] h-full pb-4">
                    {columns.map(col => (
                        <div
                            key={col.status}
                            onDragOver={onDragOver}
                            onDrop={(e) => onDrop(e, col.status)}
                            className="flex-1 min-w-[280px] flex flex-col gap-3"
                        >
                            {/* Column Header */}
                            <div className="flex items-center justify-between px-1">
                                <div className="flex items-center gap-2">
                                    <span className={`px-2 py-1 rounded text-xs font-semibold bg-gray-100 text-gray-600 flex items-center gap-2`}>
                                        <div className={`w-1.5 h-1.5 rounded-full ${col.dotColor}`} />
                                        {col.title}
                                    </span>
                                    <span className="text-xs text-gray-400 font-medium">
                                        {orders.filter(o => o.status === col.status).length}
                                    </span>
                                </div>
                                <div className="flex gap-1">
                                    <button className="text-gray-400 hover:text-gray-600 p-1">
                                        <Plus className="h-4 w-4" />
                                    </button>
                                </div>
                            </div>

                            {/* Drop Area */}
                            <div className="flex-1 space-y-3">
                                {orders.filter(o => o.status === col.status).map(order => (
                                    <div
                                        key={order.id}
                                        draggable
                                        onDragStart={(e) => onDragStart(e, order.id)}
                                        onClick={() => { setEditingOrder(order); setIsModalOpen(true); }}
                                        className="bg-white p-4 rounded-lg border border-gray-200 shadow-sm cursor-grab active:cursor-grabbing hover:shadow-md transition-all group"
                                    >
                                        <div className="space-y-2">
                                            {/* Tags */}
                                            {order.tags && order.tags.length > 0 && (
                                                <div className="flex flex-wrap gap-1 mb-2">
                                                    {order.tags.map(tag => (
                                                        <span key={tag} className={`text-[10px] font-medium px-2 py-0.5 rounded-full ${getTagColor(tag)}`}>
                                                            {tag}
                                                        </span>
                                                    ))}
                                                </div>
                                            )}

                                            <p className="font-medium text-sm text-gray-900 leading-tight">
                                                {order.itemsSummary || "Untitled Project"}
                                            </p>

                                            {/* Meta */}
                                            <div className="flex items-center gap-2 text-xs text-gray-500 mt-2">
                                                <User className="h-3 w-3" /> {order.customer}
                                            </div>

                                            {/* Footer Info */}
                                            <div className="flex items-center justify-between pt-3 mt-1">
                                                <div className="flex items-center gap-3 text-gray-400">
                                                    <span className="flex items-center gap-1 text-[10px]">
                                                        <MessageSquare className="h-3 w-3" /> 2
                                                    </span>
                                                    <span className="flex items-center gap-1 text-[10px]">
                                                        <Paperclip className="h-3 w-3" /> 1
                                                    </span>
                                                </div>
                                                <span className="text-xs font-bold text-gray-900">RM{Number(order.total).toFixed(0)}</span>
                                            </div>

                                            {/* Progress Bar (Mock) */}
                                            {order.status === 'Processing' && (
                                                <div className="w-full bg-gray-100 h-1 rounded-full mt-2 overflow-hidden">
                                                    <div className="bg-green-500 h-full w-[60%]" />
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                ))}

                                {/* "New" Ghost Button */}
                                <button
                                    onClick={() => { setEditingOrder({ status: col.status } as any); setIsModalOpen(true); }}
                                    className="flex items-center gap-2 text-gray-400 hover:text-gray-600 px-2 py-1 text-sm transition-colors"
                                >
                                    <Plus className="h-4 w-4" /> New
                                </button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Modal Reused from Context */}
            {isModalOpen && (
                <OrderModal
                    initialData={editingOrder}
                    onClose={() => setIsModalOpen(false)}
                    onSave={handleSaveOrder}
                />
            )}
        </div>
    );
}

function getTagColor(tag: string) {
    if (tag === 'Urgent' || tag === 'High') return 'bg-red-100 text-red-700';
    if (tag === 'New Client') return 'bg-blue-100 text-blue-700';
    if (tag === 'Paid') return 'bg-green-100 text-green-700';
    return 'bg-gray-100 text-gray-600';
}

function OrderModal({ initialData, onClose, onSave }: any) {
    // Reuse previous modal logic, just simplified for this snippet
    const [formData, setFormData] = useState<any>({
        customer: "",
        total: 0,
        status: "Pending",
        itemsSummary: "",
        ...initialData
    });

    const handleSubmit = (e: any) => {
        e.preventDefault();
        onSave({
            ...formData,
            id: initialData?.id || `ORD-${Math.floor(Math.random() * 10000)}`,
            date: new Date().toISOString()
        });
    };

    return (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
            <div className="bg-white rounded-xl shadow-xl w-full max-w-md p-6">
                <div className="flex justify-between items-center mb-4">
                    <h3 className="font-bold">{initialData?.id ? 'Edit' : 'New'} Order</h3>
                    <button onClick={onClose}><X className="h-5 w-5" /></button>
                </div>
                <form onSubmit={handleSubmit} className="space-y-4">
                    <input className="w-full border p-2 rounded" placeholder="Customer" value={formData.customer || ''} onChange={e => setFormData({ ...formData, customer: e.target.value })} required />
                    <input className="w-full border p-2 rounded" placeholder="Summary (e.g. 500 cards)" value={formData.itemsSummary || ''} onChange={e => setFormData({ ...formData, itemsSummary: e.target.value })} />
                    <input className="w-full border p-2 rounded" type="number" placeholder="Total" value={formData.total || 0} onChange={e => setFormData({ ...formData, total: parseFloat(e.target.value) })} required />
                    <select className="w-full border p-2 rounded" value={formData.status || 'Pending'} onChange={e => setFormData({ ...formData, status: e.target.value })}>
                        <option value="Pending">Pending</option>
                        <option value="Processing">Processing</option>
                        <option value="Completed">Completed</option>
                        <option value="Cancelled">Cancelled</option>
                    </select>
                    <button className="w-full bg-black text-white p-2 rounded hover:bg-gray-800">Save</button>
                </form>
            </div>
        </div>
    )
}
